<template>
  <b-navbar toggleable="md" type="light">
    <b-navbar-brand>
      <div>
        <img src="../assets/logo.png" id="logo" alt="Logo">
        <h>POSTRES</h>
      </div>
    </b-navbar-brand>
    <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>
      <b-container>
        <b-collapse id="nav-collapse" is-nav>
          <b-navbar-nav id="fuente-menu">
            <b-nav-item class="nav-link" to="/home">Home</b-nav-item>
            <b-nav-item class="nav-link" disabled>|</b-nav-item>
            <b-nav-item class="nav-link" to="/destacados">Destacados</b-nav-item>
            <b-nav-item class="nav-link" disabled>|</b-nav-item>
            <b-nav-item class="nav-link" to="/recetas">Recetas</b-nav-item>
            <b-nav-item class="nav-link" disabled>|</b-nav-item>
            <b-nav-item class="nav-link" to="/videos">Vídeos</b-nav-item>
            <b-nav-item class="nav-link" disabled>|</b-nav-item>
            <b-nav-item class="nav-link" to="/contacto">Contacto</b-nav-item>
          </b-navbar-nav>
        </b-collapse>
        <b-collapse id="nav-collapse" is-nav>
          
          <b-navbar-nav id="registro">
            <b-nav-item class="nav-link">
              <b-button v-b-modal.modal-prevent-closing style="background-color: #ffc2cf; color:black">Iniciar sesión</b-button>
              <b-modal
                id="modal-prevent-closing"
                ref="modal"
                title="Inicio de sesión"
                @show="resetModal"
                @hidden="resetModal"
                @ok="handleOk"
              >
                <form ref="form" @submit.stop.prevent="handleSubmit">
                  <b-form-group
                    label="Usuario"
                    label-for="name-input"
                    invalid-feedback="Name is required"
                    :state="nameState"
                  >
                    <b-form-input
                      id="name-input"
                      v-model="name"
                      :state="nameState"
                      required
                    ></b-form-input>
                  </b-form-group>
                </form>
              </b-modal>
            </b-nav-item>
          </b-navbar-nav>

          <b-navbar-nav id="registro"> 
            <b-nav-item class="nav-link">
                <b-nav-text v-b-modal.modal-prevent-closing>Registrarse</b-nav-text>
                <b-modal
                  id="modal-prevent-closing"
                  ref="modal"
                  title="Registro"
                  @show="resetModal"
                  @hidden="resetModal"
                  @ok="handleOk"
                >
                  <form ref="form" @submit.stop.prevent="handleSubmit">
                    <b-form-group
                      label="Usuario"
                      label-for="name-input"
                      invalid-feedback="Name is required"
                      :state="nameState"
                    >
                      <b-form-input
                        id="name-input"
                        v-model="name"
                        :state="nameState"
                        required
                      ></b-form-input>
                    </b-form-group>
                  </form>
                </b-modal>
              </b-nav-item>
          </b-navbar-nav>
        </b-collapse>
      </b-container>
  </b-navbar>
</template>


<script>
  export default {
    data() {
      return {
        name: '',
        nameState: null,
        submittedNames: []
      }
    },
    methods: {
      checkFormValidity() {
        const valid = this.$refs.form.checkValidity()
        this.nameState = valid
        return valid
      },
      resetModal() {
        this.name = ''
        this.nameState = null
      },
      handleOk(bvModalEvt) {
        // Prevent modal from closing
        bvModalEvt.preventDefault()
        // Trigger submit handler
        this.handleSubmit()
      },
      handleSubmit() {
        // Exit when the form isn't valid
        if (!this.checkFormValidity()) {
          return
        }
        // Push the name to submitted names
        this.submittedNames.push(this.name)
        // Hide the modal manually
        this.$nextTick(() => {
          this.$bvModal.hide('modal-prevent-closing')
        })
      }
    }
  }
</script>